"use client";

import { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";

interface HistoryRecord {
  timestamp: number;
  data: {
    temperature: number;
    movement: string;
    feedLevel: string;
    waterLevel: string;
    heartRate?: number;
    activityLevel?: number;
  };
  analysis: {
    feedStatus: string;
    waterStatus: string;
    healthStatus: string;
    diseaseIndicator?: string;
    riskLevel: 'Low' | 'Medium' | 'High';
    recommendations: string[];
  };
}

export default function HistoryPage() {
  const [historyData, setHistoryData] = useState<HistoryRecord[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [filter, setFilter] = useState<'all' | 'healthy' | 'at-risk' | 'sick'>('all');

  const fetchHistoryData = async () => {
    try {
      setError(null);
      const response = await fetch('/api/sensor?history=1');
      const result = await response.json();
      
      if (result.success) {
        setHistoryData(result.data || []);
      } else {
        setError(result.error || 'Failed to fetch history data');
      }
    } catch (err) {
      setError('Network error: Unable to fetch history data');
    } finally {
      setLoading(false);
    }
  };

  const clearHistory = async () => {
    try {
      const response = await fetch('/api/sensor', { method: 'DELETE' });
      const result = await response.json();
      
      if (result.success) {
        setHistoryData([]);
      } else {
        setError(result.error || 'Failed to clear history');
      }
    } catch (err) {
      setError('Network error: Unable to clear history');
    }
  };

  useEffect(() => {
    fetchHistoryData();
  }, []);

  const getHealthStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case 'healthy': return 'bg-green-100 text-green-800 border-green-200';
      case 'at risk': return 'bg-yellow-100 text-yellow-800 border-yellow-200';
      case 'sick': return 'bg-orange-100 text-orange-800 border-orange-200';
      case 'critical': return 'bg-red-100 text-red-800 border-red-200';
      default: return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  const getRiskLevelColor = (level: string) => {
    switch (level.toLowerCase()) {
      case 'low': return 'bg-green-100 text-green-800 border-green-200';
      case 'medium': return 'bg-yellow-100 text-yellow-800 border-yellow-200';
      case 'high': return 'bg-red-100 text-red-800 border-red-200';
      default: return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  const filteredData = historyData.filter(record => {
    if (filter === 'all') return true;
    const status = record.analysis.healthStatus.toLowerCase();
    if (filter === 'healthy') return status === 'healthy';
    if (filter === 'at-risk') return status === 'at risk';
    if (filter === 'sick') return status === 'sick' || status === 'critical';
    return true;
  });

  const getHealthSummary = () => {
    const total = historyData.length;
    if (total === 0) return { healthy: 0, atRisk: 0, sick: 0, total: 0 };

    const healthy = historyData.filter(r => r.analysis.healthStatus.toLowerCase() === 'healthy').length;
    const atRisk = historyData.filter(r => r.analysis.healthStatus.toLowerCase() === 'at risk').length;
    const sick = historyData.filter(r => ['sick', 'critical'].includes(r.analysis.healthStatus.toLowerCase())).length;

    return { healthy, atRisk, sick, total };
  };

  const summary = getHealthSummary();

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="flex items-center justify-center min-h-96">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
            <p className="text-muted-foreground">Loading history data...</p>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="container mx-auto px-4 py-8">
        <Alert className="max-w-2xl mx-auto">
          <AlertDescription className="text-center">
            <p className="mb-4">{error}</p>
            <Button onClick={fetchHistoryData}>Retry</Button>
          </AlertDescription>
        </Alert>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold text-foreground mb-2">Sensor History</h1>
          <p className="text-muted-foreground">
            Historical sensor data and AI analysis records
          </p>
        </div>
        <div className="flex gap-2 mt-4 sm:mt-0">
          <Button onClick={fetchHistoryData} variant="outline">
            Refresh
          </Button>
          <Button onClick={clearHistory} variant="destructive" size="sm">
            Clear History
          </Button>
        </div>
      </div>

      {/* Summary Cards */}
      {summary.total > 0 && (
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium">Total Records</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{summary.total}</div>
            </CardContent>
          </Card>
          
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-green-600">Healthy</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">{summary.healthy}</div>
              <p className="text-xs text-muted-foreground">
                {summary.total > 0 ? Math.round((summary.healthy / summary.total) * 100) : 0}%
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-yellow-600">At Risk</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-yellow-600">{summary.atRisk}</div>
              <p className="text-xs text-muted-foreground">
                {summary.total > 0 ? Math.round((summary.atRisk / summary.total) * 100) : 0}%
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-red-600">Sick/Critical</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-red-600">{summary.sick}</div>
              <p className="text-xs text-muted-foreground">
                {summary.total > 0 ? Math.round((summary.sick / summary.total) * 100) : 0}%
              </p>
            </CardContent>
          </Card>
        </div>
      )}

      {/* Filter Buttons */}
      <div className="flex flex-wrap gap-2 mb-6">
        <Button 
          variant={filter === 'all' ? 'default' : 'outline'} 
          size="sm"
          onClick={() => setFilter('all')}
        >
          All Records ({historyData.length})
        </Button>
        <Button 
          variant={filter === 'healthy' ? 'default' : 'outline'} 
          size="sm"
          onClick={() => setFilter('healthy')}
        >
          Healthy ({summary.healthy})
        </Button>
        <Button 
          variant={filter === 'at-risk' ? 'default' : 'outline'} 
          size="sm"
          onClick={() => setFilter('at-risk')}
        >
          At Risk ({summary.atRisk})
        </Button>
        <Button 
          variant={filter === 'sick' ? 'default' : 'outline'} 
          size="sm"
          onClick={() => setFilter('sick')}
        >
          Sick/Critical ({summary.sick})
        </Button>
      </div>

      {/* History Table */}
      {filteredData.length === 0 ? (
        <Card>
          <CardContent className="text-center py-8">
            <p className="text-muted-foreground">
              {historyData.length === 0 
                ? "No history data available. Visit the dashboard to start collecting sensor data."
                : "No records match the selected filter."
              }
            </p>
          </CardContent>
        </Card>
      ) : (
        <Card>
          <CardHeader>
            <CardTitle>Sensor Records</CardTitle>
            <CardDescription>
              Showing {filteredData.length} of {historyData.length} records
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Timestamp</TableHead>
                    <TableHead>Temperature</TableHead>
                    <TableHead>Movement</TableHead>
                    <TableHead>Feed</TableHead>
                    <TableHead>Water</TableHead>
                    <TableHead>Health Status</TableHead>
                    <TableHead>Risk Level</TableHead>
                    <TableHead>Disease Indicator</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredData.slice().reverse().map((record, index) => (
                    <TableRow key={index}>
                      <TableCell className="font-mono text-sm">
                        {new Date(record.timestamp).toLocaleString()}
                      </TableCell>
                      <TableCell>
                        <span className={
                          record.data.temperature > 41 ? 'text-red-600 font-semibold' :
                          record.data.temperature > 39.5 ? 'text-orange-600 font-semibold' :
                          record.data.temperature < 36 ? 'text-blue-600 font-semibold' :
                          'text-green-600'
                        }>
                          {record.data.temperature.toFixed(1)}Â°C
                        </span>
                      </TableCell>
                      <TableCell className="capitalize">{record.data.movement}</TableCell>
                      <TableCell>
                        <span className={
                          ['Low', 'Empty'].includes(record.data.feedLevel) ? 'text-red-600 font-semibold' : ''
                        }>
                          {record.data.feedLevel}
                        </span>
                      </TableCell>
                      <TableCell>
                        <span className={
                          ['Low', 'Empty'].includes(record.data.waterLevel) ? 'text-red-600 font-semibold' : ''
                        }>
                          {record.data.waterLevel}
                        </span>
                      </TableCell>
                      <TableCell>
                        <Badge className={getHealthStatusColor(record.analysis.healthStatus)}>
                          {record.analysis.healthStatus}
                        </Badge>
                      </TableCell>
                      <TableCell>
                        <Badge className={getRiskLevelColor(record.analysis.riskLevel)}>
                          {record.analysis.riskLevel}
                        </Badge>
                      </TableCell>
                      <TableCell className="max-w-xs">
                        <span className="text-sm text-muted-foreground">
                          {record.analysis.diseaseIndicator || 'None'}
                        </span>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
